const GenericCard = ({ headerText, content, imgSource, cardsBelow, screenSize }: { headerText: string; content: React.ReactNode; imgSource: string; cardsBelow: React.ReactNode; screenSize: string }) => {
  const isSmallScreen = useMediaQuery({ maxWidth: 767 });
  const isMediumScreen = useMediaQuery({ minWidth: 768, maxWidth: 1023 });
  const isLargeScreen = useMediaQuery({ minWidth: 1024 });
  const cardCols = isSmallScreen ? '7' : '4';
  return (
    <div className='h-full container-fluid' style={{}}>
      <div style={{ position: 'relative', overflow: 'auto', margin: 0, padding: 0 }}>
        <div className="  grid grid-rows-10 gap-0 content-start items-center justify-center h-screen  ">
          <div className='row-span-1'>
            <Header />
          </div>
          {!isSmallScreen ? null :
            <div style={{ position: 'relative' }}>
              <img src={imgSource} className={`w-full`} style={{ opacity: 0.5 }} />
              <div className="mt-10 scroll-m-20 border-b pb-2 text-2xl font-semibold transition-colors first:mt-0" style={{ position: 'absolute', top: '30%', left: '50%', transform: 'translate(-50%, -50%)', textAlign: 'center' }}>
                <p>{headerText}</p>
              </div>
            </div>}

          <div className=" w-full grid grid-cols-7 gap-0 content-start  justify-center row-span-5" style={{ margin: '10px' }}>

            <Card className={`h-${screenSize} ${isSmallScreen ? 'col-span-7' : 'col-span-4'} p-100 `} style={{ padding: '20px', marginLeft: '10px', marginRight: '10px' }}>

              {isSmallScreen ? null : <CardHeader>
                <CardTitle className="mt-10 scroll-m-20 border-b pb-2 text-2xl font-semibold tracking-tight transition-colors first:mt-0">{headerText}</CardTitle>

              </CardHeader>}
              <CardContent>

                {content}

              </CardContent>
            </Card>


            {isSmallScreen ? null : <img src={imgSource} className={`col-span-3 h-screen w-full`} style={{ objectFit: 'cover', display: 'block' }} />}


          </div>
          <div className='row-span-2 h-full'>
            {cardsBelow}
          </div >
        </div >
      </div>


      <div className={cardsBelow ? 'row-span-1 pb-2 w-screen' : 'row-span-3 pb-2 w-screen'} style={{ position: 'relative', bottom: 0 }}>
        <Footer />
      </div >
    </div >);
};
